import React, { useState, useEffect } from "react";
import Table from 'react-bootstrap/Table';
import {
  fetchAllCategories,
  addCategory,
  updateCategory,
  deleteCategory,
} from "../../services/ApiService";
import { toast } from "react-toastify";
import Skeleton from "react-loading-skeleton";
import { Button, Modal, Form } from 'react-bootstrap'; // S dng modal v form t react-bootstrap
import { useSelector } from "react-redux";

function ManageCategories() {
  const [categories, setCategories] = useState([]);
  const [newCategoryName, setNewCategoryName] = useState("");
  const [editCategoryName, setEditCategoryName] = useState("");
  const [loadingCategories, setLoadingCategories] = useState(true);
  const [showAddModal, setShowAddModal] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);
  const [categoryToEdit, setCategoryToEdit] = useState(null);
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [categoryToDelete, setCategoryToDelete] = useState(null);
  const { jwt } = useSelector((state) => state.auth);

  const fetchCategories = async () => {
    try {
      const res = await fetchAllCategories();
      setCategories(res.data);
      setLoadingCategories(false);
    } catch (error) {
      toast.error("Li khi ly danh mc:", error);
      setLoadingCategories(false);
    }
  };

  useEffect(() => {
    fetchCategories();
  }, []);

  const handleAddCategory = async () => {
    try {
      const res = await addCategory({ name: newCategoryName }, jwt);
      setNewCategoryName("");
      setShowAddModal(false);
      fetchCategories();
      toast.success(res.data);
    } catch (error) {
      toast.error("Li khi thm danh mc:", error);
    }
  };

  const handleEditCategory = async () => {
    try {
      const res = await updateCategory(categoryToEdit.id, { name: editCategoryName }, jwt);
      setEditCategoryName("");
      setShowEditModal(false);
      fetchCategories();
      toast.success(res.data);
    } catch (error) {
      toast.error("Li khi sa danh mc:", error);
    }
  };

  const confirmDeleteCategory = (id) => {
    const category = categories.find((cat) => cat.id === id);
    setCategoryToDelete(category);
    setShowDeleteModal(true);
  };

  const handleDeleteCategory = async () => {
    try {
      const res = await deleteCategory(categoryToDelete.id, jwt);
      setShowDeleteModal(false);
      setCategoryToDelete(null);
      fetchCategories();
      toast.success(res.data);
    } catch (error) {
      toast.error("Li khi xa danh mc:", error);
    }
  };

  return (
    <div style={{ maxWidth: '800px' }} className="mx-auto">
      <Button variant="primary" onClick={() => setShowAddModal(true)} className="my-3">
        Thm danh mc
      </Button>

      <Table striped bordered hover size="sm" style={{ maxWidth: '800px' }} responsive>
        <thead>
          <tr>
            <th className="text-center">M</th>
            <th className="text-center">Tn</th>
            <th className="text-center">Thao tc</th>
          </tr>
        </thead>
        <tbody>
          {loadingCategories
            ? Array(4)
                .fill()
                .map((_, index) => (
                  <tr key={index}>
                    <td><Skeleton width={40} height={20} /></td>
                    <td><Skeleton width={100} height={20} /></td>
                    <td><Skeleton width={80} height={20} /></td>
                  </tr>
                ))
            : categories.map((category) => (
                <tr key={category.id}>
                  <td className="text-center">{category.id}</td>
                  <td className="text-center">{category.name}</td>
                  <td className="text-center">
                    <Button
                      variant="info"
                      size="sm"
                      onClick={() => {
                        setCategoryToEdit(category);
                        setEditCategoryName(category.name);
                        setShowEditModal(true);
                      }}
                    >
                      Sa
                    </Button>{" "}
                    <Button
                      variant="danger"
                      size="sm"
                      onClick={() => confirmDeleteCategory(category.id)}
                    >
                      Xa
                    </Button>
                  </td>
                </tr>
              ))}
        </tbody>
      </Table>

      {/* Modal thm danh mc */}
      <Modal show={showAddModal} onHide={() => setShowAddModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Thm danh mc</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form.Control
            type="text"
            placeholder="Nhp tn danh mc"
            value={newCategoryName}
            onChange={(e) => setNewCategoryName(e.target.value)}
          />
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowAddModal(false)}>
            Hy
          </Button>
          <Button variant="primary" onClick={handleAddCategory}>
            Thm
          </Button>
        </Modal.Footer>
      </Modal>

      {/* Modal sa danh mc */}
      <Modal show={showEditModal} onHide={() => setShowEditModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Sa danh mc</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form.Control
            type="text"
            placeholder="Nhp tn danh mc"
            value={editCategoryName}
            onChange={(e) => setEditCategoryName(e.target.value)}
          />
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowEditModal(false)}>
            Hy
          </Button>
          <Button variant="primary" onClick={handleEditCategory}>
            Lu thay i
          </Button>
        </Modal.Footer>
      </Modal>

      {/* Modal xc nhn xa */}
      <Modal show={showDeleteModal} onHide={() => setShowDeleteModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Xc nhn xa</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          Bn c mun xa danh mc "{categoryToDelete?.name}" khng khi tt c mn n thuc danh mc ny u b xa?
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowDeleteModal(false)}>
            Hy
          </Button>
          <Button variant="danger" onClick={handleDeleteCategory}>
            Xa
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}

export default ManageCategories;
