import React, { useEffect, useState } from "react";
import {
  Table,
  Button,
  Spinner,
  Pagination,
  Modal,
  Form,
} from "react-bootstrap";
import {
  fetchAllOrders,
  updateOrderStatus,
  deleteOrder,
} from "../../services/ApiService";
import { useSelector } from "react-redux";
import { toast } from "react-toastify";
import { Link } from "react-router-dom";
import * as XLSX from "xlsx";
import { saveAs } from "file-saver";
import Big from "big.js";

const ManageAllOrder = () => {
  const [orders, setOrders] = useState([]); // D liu orders
  const [currentPage, setCurrentPage] = useState(0); // Trang hin ti
  const [totalPages, setTotalPages] = useState(0); // Tng s trang
  const [loading, setLoading] = useState(false); // Trng thi ti d liu
  const [showModal, setShowModal] = useState(false); // Trng thi modal
  const [modalType, setModalType] = useState(""); // Loi modal: "update" hoc "delete"
  const [selectedOrder, setSelectedOrder] = useState(null); // Order c chn
  const [paymentStatus, setPaymentStatus] = useState(""); // Trng thi thanh ton mi

  // Ly JWT t Redux store
  const { jwt } = useSelector((state) => state.auth);

  // Gi API ly danh sch Orders
  const fetchOrders = async (page) => {
    setLoading(true);
    try {
      const response = await fetchAllOrders(page, jwt);
      setOrders(response.data.content); // Gn d liu orders
      setTotalPages(response.data.totalPages); // Gn tng s trang
      setCurrentPage(response.data.number); // Gn s trang hin ti
    } catch (error) {
      console.error("Error fetching orders:", error);
    } finally {
      setLoading(false);
    }
  };

  // X l m Modal
  const handleOpenModal = (type, order) => {
    setModalType(type); // Gn loi modal
    setSelectedOrder(order); // Gn order c chn
    setShowModal(true); // Hin th modal
    if (type === "update") setPaymentStatus(order.paymentStatus); // Gn trng thi ban u
  };

  // ng Modal
  const handleCloseModal = () => {
    setShowModal(false);
    setSelectedOrder(null);
    setPaymentStatus("");
  };

  // X l Update Payment Status
  const handleUpdatePaymentStatus = async () => {
    if (!selectedOrder) return;
    try {
      await updateOrderStatus(selectedOrder.id, paymentStatus, jwt); // Gi API
      toast.success("Payment status updated successfully!");
      handleCloseModal(); // ng modal
      fetchOrders(currentPage); // Lm mi danh sch
    } catch (error) {
      console.error("Error updating payment status:", error);
      toast.error("Failed to update payment status.");
    }
  };

  // X l Delete Order
  const handleDeleteOrder = async () => {
    if (!selectedOrder) return;
    try {
      await deleteOrder(selectedOrder.id, jwt); // Gi API
      toast.success("Order deleted successfully!");
      handleCloseModal(); // ng modal
      fetchOrders(currentPage); // Lm mi danh sch
    } catch (error) {
      console.error("Error deleting order:", error);
      toast.error("Failed to delete order.");
    }
  };

  // Gi API khi component mount hoc currentPage thay i
  useEffect(() => {
    fetchOrders(currentPage);
  }, [currentPage]);

  // Hm chuyn trang
  const handlePageChange = (page) => {
    if (page >= 0 && page < totalPages) {
      setCurrentPage(page);
    }
  };
  const exportOrderToExcel = (order) => {
    const workbook = XLSX.utils.book_new();

    // Header thng tin chi tit n hng
    const sheetData = [
      ["Chi Tit n Hng"], // Tiu 
      [], // Dng trng
      ["Thi Gian t:", new Date(order.orderTime).toLocaleString()],
      ["Trng Thi Thanh Ton:", order.paymentStatus],
      [], // Dng trng
      ["Tn Mn", "S Lng", "Gi"], // Header bng
    ];

    // Cu hnh mu sc v nh dng cho cc ct
    const headerStyle = {
      fill: {
        fgColor: { rgb: "FFFF00" }, // Mu vng cho header
      },
      font: { bold: true }, // Ch m cho header
      alignment: { horizontal: "center" },
    };

    // D liu mn n
    order.orderItems.forEach((item) => {
      // Kim tra gi tr price c phi l s hp l khng
      let priceFormatted = item.food.price;
      if (
        isNaN(priceFormatted) ||
        priceFormatted === null ||
        priceFormatted === undefined
      ) {
        priceFormatted = "0"; // Nu khng hp l, gn gi tr mc nh
      } else {
        priceFormatted = new Big(priceFormatted).toLocaleString(); // m bo x l kiu BigDecimal
      }

      sheetData.push([item.food.name, item.quantity, `${priceFormatted} VND`]);
    });

    // Tng gi
    sheetData.push([]); // Dng trng
    let totalPriceFormatted = order.totalPrice;
    if (
      isNaN(totalPriceFormatted) ||
      totalPriceFormatted === null ||
      totalPriceFormatted === undefined
    ) {
      totalPriceFormatted = "0"; // Nu khng hp l, gn gi tr mc nh
    } else {
      totalPriceFormatted = new Big(totalPriceFormatted).toLocaleString(); // m bo x l kiu BigDecimal
    }

    sheetData.push(["Tng Gi:", "", `${totalPriceFormatted} VND`]);

    // To worksheet
    const worksheet = XLSX.utils.aoa_to_sheet(sheetData);

    // Thm style cho header (cn gia v mu sc)
    const range = XLSX.utils.decode_range(worksheet["!ref"]); // Ly phm vi d liu
    for (let col = range.s.c; col <= range.e.c; col++) {
      const cellAddress = { r: 0, c: col }; // Chnh sa header
      if (!worksheet[XLSX.utils.encode_cell(cellAddress)]) continue;
      worksheet[XLSX.utils.encode_cell(cellAddress)].s = headerStyle;
    }

    // T ng cn chnh ct
    const colWidths = [];
    for (let col = 0; col <= range.e.c; col++) {
      let maxWidth = 0;
      for (let row = range.s.r; row <= range.e.r; row++) {
        const cellAddress = { r: row, c: col };
        const cell = worksheet[XLSX.utils.encode_cell(cellAddress)];
        if (cell && cell.v) {
          maxWidth = Math.max(maxWidth, cell.v.toString().length);
        }
      }
      colWidths.push({ wch: maxWidth + 2 }); // Thm cht khong trng cho d nhn
    }
    worksheet["!cols"] = colWidths; // p dng chiu rng ct t ng

    // Thm worksheet vo workbook
    XLSX.utils.book_append_sheet(workbook, worksheet, `Order_${order.id}`);

    // Xut file Excel
    const excelBuffer = XLSX.write(workbook, {
      bookType: "xlsx",
      type: "array",
    });
    const blob = new Blob([excelBuffer], { type: "application/octet-stream" });
    saveAs(blob, `Order_${order.id}.xlsx`);
  };

  return (
    <div className="container mt-4">
      <Link to="/ManageOrders">
        <Button variant="primary">New Order</Button>
      </Link>
      <h1 className="text-center mb-4">Manage All Orders</h1>
      {loading ? (
        <div className="text-center">
          <Spinner animation="border" role="status">
            <span className="visually-hidden">Loading...</span>
          </Spinner>
        </div>
      ) : (
        <>
          <Table striped bordered hover responsive>
            <thead className="table-dark">
              <tr>
                <th>ID</th>
                <th>Order Time</th>
                <th>Payment Status</th>
                <th>Total Price</th>
                <th>Method</th>
              </tr>
            </thead>
            <tbody>
              {orders.length > 0 ? (
                orders.map((order) => (
                  <tr key={order.id}>
                    <td>{order.id}</td>
                    <td>{order.orderTime}</td>
                    <td>{order.paymentStatus}</td>
                    <td>${order.totalPrice.toFixed(2)}</td>
                    <td>
                      <Button
                        variant="success"
                        size="sm"
                        onClick={() => exportOrderToExcel(order)}
                      >
                        In Ha n
                      </Button>

                      <Button
                        variant="warning"
                        size="sm"
                        className="me-2"
                        onClick={() => handleOpenModal("update", order)}
                      >
                        Update Payment Status
                      </Button>
                      <Button
                        variant="danger"
                        size="sm"
                        onClick={() => handleOpenModal("delete", order)}
                      >
                        Delete Order
                      </Button>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan="5" className="text-center">
                    No orders found.
                  </td>
                </tr>
              )}
            </tbody>
          </Table>

          {/* Phn trang */}
          <Pagination className="justify-content-center mt-4">
            <Pagination.Prev
              onClick={() => handlePageChange(currentPage - 1)}
              disabled={currentPage === 0}
            >
              Previous
            </Pagination.Prev>
            {[...Array(totalPages).keys()].map((page) => (
              <Pagination.Item
                key={page}
                active={page === currentPage}
                onClick={() => handlePageChange(page)}
              >
                {page + 1}
              </Pagination.Item>
            ))}
            <Pagination.Next
              onClick={() => handlePageChange(currentPage + 1)}
              disabled={currentPage === totalPages - 1}
            >
              Next
            </Pagination.Next>
          </Pagination>
        </>
      )}

      {/* Modal */}
      <Modal show={showModal} onHide={handleCloseModal}>
        <Modal.Header closeButton>
          <Modal.Title>
            {modalType === "update" ? "Update Payment Status" : "Delete Order"}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {modalType === "update" ? (
            <>
              <Form.Group>
                <Form.Label>Payment Status</Form.Label>
                <Form.Control
                  as="select"
                  value={paymentStatus}
                  onChange={(e) => setPaymentStatus(e.target.value)}
                >
                  <option value="PAID">Paid</option>
                  <option value="UNPAID">Unpaid</option>
                </Form.Control>
              </Form.Group>
            </>
          ) : (
            <p>Are you sure you want to delete this order?</p>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleCloseModal}>
            Cancel
          </Button>
          {modalType === "update" ? (
            <Button variant="warning" onClick={handleUpdatePaymentStatus}>
              Update
            </Button>
          ) : (
            <Button variant="danger" onClick={handleDeleteOrder}>
              Delete
            </Button>
          )}
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default ManageAllOrder;
